<template>
  <li class="list-item" @click="toDetail">
    <div class="pic">
      <img :src="`http://statics.zhuishushenqi.com${book.cover}`" />
    </div>
    <div class="info">
      <div class="title">{{ book.title }}</div>
      <div class="desc">
        {{ book.shortIntro }}
      </div>
      <div class="bottom">
        <span class="author">{{ book.author }}</span>
        <span class="cate">{{ book.majorCate?book.majorCate:book.cat }}</span>
      </div>
    </div>
  </li>
</template>

<script>
export default {
  name: "ListItem",
  props: ["book"],
  methods: {
    toDetail() {
      this.$router.push({
        name: "BookDetail",
        query: { id: this.book._id },
      });
    },
  },
};
</script>

<style lang="less" scoped>
.list-item {
  margin: 10px 8px;
  display: flex;
  .pic {
    img {
      display: block;
      width: 90px;
    }
  }
  .info {
    flex: 1;
    margin: 0 8px;
    position: relative;
    .title {
      font-size: 16px;

      word-wrap: break-word;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 1; /*行数*/
      -webkit-box-orient: vertical;
    }
    .desc {
      font-size: 13px;
      margin: 10px 0;
      color: #999;

      word-wrap: break-word;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 3; /*行数*/
      -webkit-box-orient: vertical;
    }
    .bottom {
      font-size: 12px;
      position: absolute;
      // padding-top: 8px;
      width: 100%;
      bottom: 0;
      .author {
        color: #666;
      }
      .cate {
        position: absolute;
        right: 0;
        color: #777;
        background-color: #efeff4;
        border-radius: 8px;
        padding: 0 6px;
      }
    }
  }
}
</style>