<template>
  <div class="search">
    <div class="header">
      <van-search
        v-model="msg"
        shape="round"
        placeholder="请输入关键字"
        @focus="focus"
      />
    </div>
    <div class="main" v-if="!$route.query.name">
      <div class="recommend" v-if="isRecommend">
        <div class="title">热门推荐</div>
        <ul class="hot">
          <li
            v-for="(item, index) in searchHotWords"
            :key="index"
            @click="navigate(item.word)"
          >
            {{ item.word }}
          </li>
        </ul>

        <div class="history" v-if="searchHistory.length > 0">
          <div class="title">搜索历史</div>
          <ul>
            <li v-for="(item, index) in searchHistory" :key="index">
              <div class="name" @click="navigate(searchHistory[index])">
                {{ searchHistory[index] }}
              </div>
              <div class="delete" @click="deleteHistory(index)">
                <van-icon name="cross" />
              </div>
            </li>
          </ul>
          <div class="clear-all" @click="clearAll">清空所有历史记录</div>
        </div>
      </div>
      <div class="searching" v-else>
        <ul class="content" v-if="searchs">
          <li
            v-for="search in searchs.slice(0, 10)"
            :key="search._id"
            @click="navigate(search.title)"
          >
            {{ search.title }}
            <van-divider
              :style="{
                backgroundColor: '#fff',
                borderColor: '#ddd',
                margin: 0,
                padding: '0 10px',
              }"
            />
          </li>
        </ul>
      </div>
    </div>
    <div class="search-book" v-else>
      <van-skeleton
        title
        avatar
        :row="3"
        :loading="loading"
        avatar-shape="square"
      >
        <ul class="content">
          <ListItem
            v-for="(item, index) in searchs"
            :key="index"
            :book="item"
          ></ListItem>
        </ul>
      </van-skeleton>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import ListItem from "@/components/ListItem.vue";

export default {
  name: "Search",
  components: {
    ListItem,
  },
  data() {
    return {
      searchHotWords: [],
      searchs: null,
      msg: "",
      loading: true,
    };
  },
  computed: {
    ...mapState(["searchHistory", "isRecommend"]),
  },
  created() {
    this.axios.get("/search-hotwords").then((response) => {
      // console.log(response.data.searchHotWords.slice(0, 10));
      this.searchHotWords = response.data.searchHotWords.slice(0, 10);
    });

    if (this.$route.query.name) {
      this.axios
        .get(`/search?keyword=${this.$route.query.name}`)
        .then((response) => {
          // console.log(response.data);
          this.searchs = response.data.books;
          this.loading = false;
          this.msg = this.$route.query.name;
        })
        .catch(() => {});
    }
  },
  watch: {
    msg() {
      if (!this.msg.trim()) {
        this.$store.commit("updateIsRecommend", true);
        return;
      }
      clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        if (this.msg == "") {
          return;
        }
        this.$store.commit("updateIsRecommend", false);
        this.axios
          .get(`/search?keyword=${this.msg}`)
          .then((response) => {
            // console.log(response.data);
            this.searchs = response.data.books;
          })
          .catch(() => {});
      }, 500);
    },
    "$route.query.name"() {
      this.axios
        .get(`/search?keyword=${this.msg}`)
        .then((response) => {
          // console.log(response.data);
          this.searchs = response.data.books;
          this.loading = false;
        })
        .catch(() => {});
    },
  },
  beforeRouteLeave(to, from, next) {
    console.log("search--beforeRouteLeave");
    this.$store.commit("updateIsRecommend", true);
    next();
  },
  methods: {
    focus() {
      if (this.$route.name == "Search" && !this.$route.query.name) {
        console.log();
        return;
      }
      this.$router.push({ name: "Search" });
    },
    navigate(name) {
      this.msg = name;
      this.$router.push({ name: "Search", query: { name: name } });
      this.$store.commit("updateSearchHistory", this.$route.query.name);
    },
    deleteHistory(index) {
      var history = this.searchHistory.concat();
      history.splice(index, 1);
      // console.log(history);

      this.$store.commit("resetSearchHistory", history);
    },
    clearAll() {
      this.$store.commit("resetSearchHistory", []);
    },
  },
};
</script>

<style lang="less" scoped>
.search {
  .header {
    .van-search {
      padding: 10px 20px 0 20px;
      margin-bottom: 10px;
    }
  }
  .main {
    .recommend {
      margin: 10px 20px;
      .title {
        font-size: 18px;
        margin-bottom: 5px;
      }
      ul.hot {
        li {
          font-size: 13px;
          border-radius: 12px;
          margin: 8px 0;
          margin-left: 10px;
          padding: 3px 10px;
          display: inline-block;
          background-color: #eee;
        }
      }
      .history {
        margin: 40px 10px;
        .title {
          font-size: 18px;
          margin-bottom: 5px;
        }
        ul {
          margin-top: 10px;
          li {
            font-size: 13px;
            display: flex;
            line-height: 35px;
            margin: 0 10px;
            border-bottom: 1px solid #ddd;
            &:first-of-type {
              border-top: 1px solid #ddd;
            }
            .name {
              flex: 1;
            }
            .delete {
              display: flex;
              align-items: center;
            }
          }
        }
        .clear-all {
          margin-top: 10px;
          font-size: 14px;
          color: #3d73dd;
          text-align: center;
        }
      }
    }
    .searching {
      position: absolute;
      top: 50px;
      width: 100vw;
      z-index: 10;
      background-color: #fff;
      height: calc(100vh - 100px); //calc(100vh - 96px)
      overflow-y: auto;
      .content {
        li {
          text-align: left;
          padding: 5px 0;
          // border-bottom: 1px solid #eee;
          line-height: 25px;
          text-indent: 12px;
          font-size: 12px;
          word-wrap: break-word;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 1; /*行数*/
          -webkit-box-orient: vertical;
          .split {
            margin: 0 5px;
          }
        }
      }
    }
  }
}
</style>